.file-input{
    margin-bottom: 2em;
}
.select{
    margin-bottom: 2em;
    display: flex;
    &__title{
        margin-right: 1em;
    }
}
.stats{
    margin-bottom: 1em;
    &__title{
        padding: 10px;
        background-color: #3032C1;
        color:white;
    }
    &__results{
        display:flex;
        flex-wrap: nowrap;
        background-color: white;
        border-bottom: 1px solid lightgrey;
        border-right: 1px solid lightgrey;
        border-left: 1px solid lightgrey;
    }
    &__results-div{
        padding: 10px;
        width: 33%;
        &--border{
            border-right: 1px solid lightgrey;
        }
    }
}

.table-title{
    text-align: center;
}

.table-margin{
    margin: 10px;
}

.table-result{
    display: flex;
    margin-bottom: 1em;
    flex-wrap: wrap;
    &__header{
        flex:100%;
        background-color: #3032C1;
        color: white;
        padding: 15px;
    }
    &__row{
        flex: 100%;
        background-color: #F5F5F5;
        padding: 10px;
        &--red{
            color:red;
        }
        &--green{
            color:green;
        }
        &--overflow{
            overflow-wrap: break-word;
        }
    }
    &__column{
        width: 49%;
        overflow-wrap: break-word;
        &--border-left{
            border-left: 1px solid lightgrey;
        }
        &--time-span{
            margin-bottom: 1em;
        }
    }
    &__column-container{
        width:100%;
        display: flex;
        flex-wrap: nowrap;
    }
}
